export default (Vue: any, vm: any) => {
    console.log(vm)
    Vue.filter('sex', function (value: any) {
        let result = '';
        if (value == 1) {
            result = ''
        } else if (value == 2) {
            result = ''
        }
        return result;
    })

    /**n
     * @param {type} 
     * @return: 
     */
    Vue.filter('number', function (num: number | string, decimal = 2) {
        num = num.toString();
        let index = num.indexOf('.');
        if (index !== -1) {
            num = num.substring(0, decimal + index + 1)
        } else {
            num = num.substring(0)
        }
        return parseFloat(num).toFixed(decimal)
    })
    /**n
    * @param {type} 
    * @return: 
    */
    Vue.filter('toFixed', function (num: number | string, decimal = 2) {
        let n = parseFloat(num.toString());
        return n.toFixed(decimal)
    })

    Vue.filter('resourceType', function (value: any) {
        let result = null;
        //Root-System-Service-Menu-Api-APIButton-Element-
        switch (value) {
            case 'Root':
                result = ''
                break;
            case 'System':
                result = ''
                break;
            case 'Service':
                result = ''
                break;
            case 'Menu':
                result = ''
                break;
            case 'Api':
                result = 'API'
                break;
            case 'Button':
                result = ''
                break;

            case 'Element':
                result = ''
                break;
            default:
                result = "";
        }
        return result;
    })

};
