export default (Vue: any, vm: any) => {
    function has(el: any, binding: any) {
        let list = vm ? vm.$root.permissionsList : [];//
        let result = false;//
        if (binding.value instanceof Array) {
            //
            let set1 = new Set(binding.value);
            let set2 = new Set(list);
            //
            let intersect = [...new Set([...set1].filter(x => set2.has(x)))];
            if (intersect.length == 0) {
                result = false;
            } else {
                result = true;
            }
        } else if (typeof binding.value == 'string') {
            //
            result = list.includes(binding.value);
        } else {
            result = false;
            console.error(':' + typeof binding.value)
        }
        if (result) {
            el.className = el._prevClass + '';
        } else {
            el.className = el._prevClass + ' ih-has-hide';
            // el.remove();  dom
        }
    }


    //
    Vue.directive("has", {
        inserted(el: any, binding: any) {
            has(el, binding)
        },
        update(el: any, binding: any) {
            has(el, binding)
        },

    });

    // v-dialogDrag: 
    Vue.directive('dialogDrag', {
        bind(el: any) {
            const dialogHeaderEl = el.querySelector('.el-dialog__header')
            const dragDom = el.querySelector('.el-dialog')
            dialogHeaderEl.style.cursor = 'move'

            //  ie dom.currentStyle  window.getComputedStyle(dom, null);
            const sty = dragDom.currentStyle || window.getComputedStyle(dragDom, null)

            dialogHeaderEl.onmousedown = (e: any) => {
                // 
                const disX = e.clientX - dialogHeaderEl.offsetLeft
                const disY = e.clientY - dialogHeaderEl.offsetTop

                // px 
                let styL: any, styT: any;

                // ie 50% px
                if (sty.left.includes('%')) {
                    styL = +document.body.clientWidth * (+sty.left.replace(/%/g, '') / 100)
                    styT = +document.body.clientHeight * (+sty.top.replace(/%/g, '') / 100)
                } else {
                    styL = +sty.left.replace(/\px/g, '')
                    styT = +sty.top.replace(/\px/g, '')
                }

                document.onmousemove = function (e) {
                    // 
                    const l = e.clientX - disX
                    const t = e.clientY - disY

                    // 
                    dragDom.style.left = `${l + styL}px`
                    dragDom.style.top = `${t + styT}px`

                    // 
                    // binding.value({x:e.pageX,y:e.pageY})
                }

                document.onmouseup = function () {
                    document.onmousemove = null
                    document.onmouseup = null
                }
            }
        }
    });
};
